@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}


.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}

.Badges__buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin: 1rem 0px;
}

.Badges__buttons .btn {
  margin: 0px 10px;
}

.search:hover {
  background-color: rgb(91, 161, 37) !important;
  border-color: rgb(91, 161, 37) !important;
}

.more {
  background-color: #1C3643;
  color: white;
  width: 100%
}

.more:hover {
  cursor: pointer;
  background-color: rgba(28, 54, 67, 0.774);
  color: white;
}

.home {
  background-color: rgb(47, 114, 190) !important;
  border-color: rgb(47, 114, 190) !important;
}

.home:hover {
  background-color: rgb(29, 75, 128) !important;
  border-color: rgb(29, 75, 128) !important;
}
.list-unstyled {
    display: grid;
    grid-template-columns: repeat(3, 280px);
    grid-gap: 5px;
    box-sizing: border-box;
}

.list__element {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    box-shadow: 4px 4px 0px 0px rgba(0,0,0, .1);
    border-radius: 10px;
    background-color: white;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 0.8rem;
}

.list__element-content {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.pokemonAvatar {
    height: 96px;
    width: 96px;
}

.list__element-content-info {
    display: -webkit-flex;
    display: flex;
    text-align: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding-left: 10px;
}

.list__element-content-info h3 {
    text-transform: capitalize;
}

.list__element-content-info p {
    margin: 0;
}

.pokemonTypes {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin-top: 8px;
}

.pokemonType {
    width: 40px;
    height: 15px;
}

.details {
    /* background-color: rgba(28,54,67, 0.1); */
    background-color: rgba(96, 210, 255, 0.2);
    padding: 0.1rem 1rem;
    margin-top: 10px;
}

.details:hover {
    background-color: rgba(96, 210, 255, 0.596);
}
.loading {
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
}

.loading__msg {
    font-weight: bold;
    margin: 0px;
}

.loading__img {
    width: 30%;
}
.Error__container {
    text-align: center;
}

.Error__IMG {
    margin-top: 50px;
    width: 50%
}
.Badges__hero {
    width: 100%;
    padding: 2rem 0;
    background: url(/pokedex/static/media/stars.1d17360a.svg), #1B1B25;
    background-repeat: repeat;
  }
  
  .Badges__hero>.Badges__container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .Badges__container {
    width: 100%;
    max-width: 850px;
    margin: 0 auto;
  }
  
  .Badges_pokedex-logo {
    margin-bottom: 2rem;
  }
  


  
.BadgeNew__hero {
    width: 100%;
    padding: 1rem 1rem;
    background: url(/pokedex/static/media/stars.1d17360a.svg), #1B1B25;
    background-repeat: repeat;
    margin-bottom: 1rem;
    color: #ffffff;
  }

  .BadgeNew__hero img {
    height: 50px;
  }
.Badge {
    background: #FFFFFF;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.10);
    border-radius: 8px 8px 8px 8px;
    overflow: hidden;
    height: 380px;
  }
  
  .Badge__header {
    padding: 0.5rem 0;
    height: 80px;
    background: #1B1B25;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .Badge__section-name {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 1rem 0;
  }

  .Badge__Name {
    text-transform: capitalize;
  }
  
  .Badge__section-info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 0.5rem 0;
    background: #F4F4F7;
  }
  
  
  .Badge__avatar {
    border-radius: 50%;
    margin-right: 1rem;
    width: 120px;
    height: 120px;
  }
  
  .Badge__footer {
    height: 54px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: #EE8329;
    font-weight: bold;
    font-style: italic;
  }
.save {
    background-color: rgba(28, 54, 67, 0.2);
}
.container_404 {
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.Notfound img {
    display: block;
}

.Notfound .back {
    display: block;
    background-color: #F4F4F7;
    margin: 30px auto;
    border: 1px solid #343434;
    color: #343434;
    border-radius: 5px;
    width: 25%;
}
.Navbar {
    width: 100%;
    padding: 0.5rem 0;
    background-color: #1C3643;
  }
  
  .Navbar__brand {
    color: #ffffff; 
    display: -webkit-flex; 
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .Navbar__brand:hover {
    color: #ffffff;
    text-decoration: none;
  }
  
  .Navbar__brand-logo {
    margin-right: 0.5rem;
  }
.Pokemon__details,
.Pokemon__stats,
.Evolution_chain {
    background: white;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 10px;
    margin-left: 0px;
    margin-right: 0px;
}

.Pokemon__name {
    text-transform: capitalize;
}

.Pokemon__height,
.Pokemon__weight {
    font-size: 12px;
}

.Pokemon__types {
    padding: 0px;
    text-align: center;
}

.Pokemon__types-badges {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    height: 70%;
}

.Pokemon__type {
    width: 40px;
    height: 15px;
}

.Pokemon__header, 
.Pokemon__sprites-default, 
.Pokemon__sprites-shiny {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.Pokemon__sprites-default img, 
.Pokemon__sprites-shiny img {
    height: 96px;
    width: 96px;
}

.Pokemon__stats,
.Pokemon__Navigate,
.Evolution_chain {
    margin-top: 15px;
    padding-bottom: 0px;
}

.Pokemon__stats-speed,
.Pokemon__stats-special_defense,
.Pokemon__stats-special_attack,
.Pokemon__stats-attack,
.Pokemon__stats-defense,
.Pokemon__stats-hp {
    padding: 0px;
    text-align: center;
    text-transform: capitalize;
}

.Evolution_chain {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.Evolution_chain_first,
.Evolution_chain_second,
.Evolution_chain_third {
    text-align: center;
    text-transform: capitalize;
    font-weight: bold;
    margin: 0 20px;
}

.Pokemon__Navigate {
    display: -webkit-flex;
    display: flex;
    text-align: center;
    padding: 0px 25px;
}

.Pokemon__Navigate a,
.Evolution_chain_link {
    color: black;
    text-decoration: none !important;
}

.Evolution_chain_link:hover {
    color: black;
}

.Pokemon__Navigate-prev,
.Pokemon__Navigate-next {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    font-weight: bold;
}

.Next {
    width: 32px;
    height: 32px;
}

.Pokemon__Navigate-prev {
    margin-right: auto;
}

.Pokemon__Navigate-next {
    margin-left: auto;
}

.Pokemon__Navigate img {
    height: 60px;
    width: 60px;
}


.details__container {
    max-width: 850px;
    margin: 0 auto;
    height: 100%;
}

